import React, { useState, useEffect, useRef, useContext, useMemo } from 'react';
import { Box, Avatar, Typography, IconButton, styled, Card, CircularProgress, Button } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import CloseIcon from '@mui/icons-material/Close';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeOffIcon from '@mui/icons-material/VolumeOff';
import VideocamIcon from '@mui/icons-material/Videocam';
import { useNavigate } from 'react-router-dom';
import StoryViewer from './StoryViewer';
import * as StoryViewerComponents from './StoryViewer';
import { AuthContext } from '../../context/AuthContext';
import { useStoriesFeed, useStoryActions, viewStory } from './storiesApi';

// –î–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏—Ä—É–µ–º –Ω—É–∂–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –∏–∑ –∏–º–ø–æ—Ä—Ç–∞
const { 
  StoryContentContainer, 
  MediaContainer, 
  Media: StoryMedia, 
  BlurredMediaBackground,
  ProgressContainer,
  Header, 
  UserInfo, 
  UserAvatar 
} = StoryViewerComponents;

const StoriesCard = styled(Card)(({ theme }) => ({
  borderRadius: '12px',
  overflow: 'hidden',
  boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
  background: 'rgba(255, 255, 255, 0.03)',
  backdropFilter: 'blur(20px)',
  WebkitBackdropFilter: 'blur(20px)', 
 border: theme.palette.mode === 'dark'   
    ? '1px solid rgba(255, 255, 255, 0.1)' 
    : '1px solid rgba(0, 0, 0, 0.1)',
  marginBottom: 0
}));

const StoriesContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  gap: 2,
  overflowX: 'auto',
  padding: '16px',
  '&::-webkit-scrollbar': {
    height: '0px',
    display: 'none'
  },
  '&::-webkit-scrollbar-thumb': {
    backgroundColor: 'transparent',
  },
  '&::-webkit-scrollbar-track': {
    backgroundColor: 'transparent',
  },
  msOverflowStyle: 'none',
  scrollbarWidth: 'none',
}));

const StoryItem = styled(Box)(({ theme, hasStory }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  cursor: 'pointer',
  position: 'relative',
  '&:hover': {
    transform: 'scale(1.02)',
    transition: 'transform 0.2s ease',
  },
}));

const StoryPreview = styled(Box)(({ theme }) => ({
  width: 64,
  height: 64,
  borderRadius: '50%',
  overflow: 'hidden',
  border: `2px solid ${theme.palette.primary.main}`,
  
  background: theme.palette.background.paper,
'&::before': {
    content: '""',
    position: 'absolute',
    top: -2,
    left: -2,
    right: -2,
    bottom: -2,
    borderRadius: '50%',
    zIndex: -1,
  },
}));

const AddStoryButton = styled(IconButton)(({ theme }) => ({
  width: 64,
  height: 64,
  border: `2px solid ${theme.palette.primary.main}`,
  backgroundColor: theme.palette.background.paper,
  '&:hover': {
    backgroundColor: 'rgba(208, 188, 255, 0.1)',
  },
}));

const PreviewActionBox = styled(Box)(({ theme }) => ({
  position: 'absolute',
  bottom: 16,
  left: 0,
  right: 0,
  display: 'flex',
  justifyContent: 'center',
  gap: 16,
  zIndex: 100, // –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –∫–Ω–æ–ø–∫–∏ –ø–æ–≤–µ—Ä—Ö –ø—Ä–µ–¥–ø—Ä–æ—Å–º–æ—Ç—Ä–∞
}));

const Stories = ({ userIdentifier = null }) => {
  const { stories, loading, loadStories, setStories } = useStoriesFeed(userIdentifier);
  const { publish: publishStory } = useStoryActions();
  const [currentUserIndex, setCurrentUserIndex] = useState(null);
  const [previewFile, setPreviewFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState(null);
  const [isMuted, setIsMuted] = useState(true);
  const [isMobile, setIsMobile] = useState(false);
  const [viewedStories, setViewedStories] = useState(new Set());
  const fileInputRef = useRef(null);
  const videoRef = useRef(null);
  const { user } = useContext(AuthContext);

  // –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ —Ç–∏–ø–∞ 'user', –Ω–µ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
  // –î–ª—è –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∏—Å—Ç–æ—Ä–∏–π –¥—Ä—É–≥–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–µ –ø—Ä–æ–≤–µ—Ä—è–µ–º —Ç–∏–ø –∞–∫–∫–∞—É–Ω—Ç–∞
  if (!userIdentifier && (!user || user.account_type !== 'user')) {
    return null;
  }

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ —Ç–µ–∫—É—â–∏–π –ø—Ä–æ—Ñ–∏–ª—å –ø—Ä–æ—Ñ–∏–ª–µ–º –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  const isOwnProfile = userIdentifier === user?.id;

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –º–æ–±–∏–ª—å–Ω—ã–º
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.matchMedia('(max-width: 600px)').matches);
    };
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  // –û—Ç—Å–ª–µ–∂–∏–≤–∞–µ–º –∏–∑–º–µ–Ω–µ–Ω–∏–µ –≥—Ä–æ–º–∫–æ—Å—Ç–∏ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞—Ö
  useEffect(() => {
    if (!isMobile || !videoRef.current) return;

    const handleVolumeChange = (e) => {
      if (e.target.volume > 0) {
        setIsMuted(false);
      }
    };

    const video = videoRef.current;
    video.addEventListener('volumechange', handleVolumeChange);

    return () => {
      video.removeEventListener('volumechange', handleVolumeChange);
    };
  }, [isMobile]);

  useEffect(() => {
    loadStories();
  }, [loadStories]);

  useEffect(() => {
    if (!stories || stories.length === 0) return;
    setViewedStories(() => {
      const viewed = new Set();
      stories.forEach(storyGroup => {
        storyGroup.stories.forEach(story => {
          if (story.view_count > 0) {
            viewed.add(story.id);
          }
        });
      });
      return viewed;
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [stories && stories.length > 0]);

  // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –∏—Å—Ç–æ—Ä–∏–∏ –≤ –ø–ª–æ—Å–∫–∏–π –º–∞—Å—Å–∏–≤
  const flattenedStories = useMemo(() => {
    if (!stories || stories.length === 0) return [];
    return stories.flatMap(storyGroup => 
      storyGroup.stories.map(story => ({
        ...story,
        user: storyGroup.user
      }))
    ).sort((a, b) => {
      const aViewed = a.view_count > 0;
      const bViewed = b.view_count > 0;
      if (aViewed !== bViewed) {
        return aViewed ? 1 : -1;
      }
      const aDate = new Date(a.created_at_utc || a.created_at);
      const bDate = new Date(b.created_at_utc || b.created_at);
      return bDate - aDate;
    });
  }, [stories]);

  const handleStoryClick = (storyId) => {
    const idx = flattenedStories.findIndex(s => s.id === storyId);
    setCurrentUserIndex(idx);
  };

  const handleCloseStory = () => {
    setCurrentUserIndex(null);
  };

  const handleAddStory = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (!file) return;
    setPreviewFile(file);
    setPreviewUrl(URL.createObjectURL(file));
  };

  const handleCancelPreview = () => {
    setPreviewFile(null);
    setPreviewUrl(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handlePublishStory = async () => {
    if (!previewFile) return;
    try {
      await publishStory(previewFile);
      await loadStories(); // –û–±–Ω–æ–≤–ª—è–µ–º —Å–ø–∏—Å–æ–∫ –∏—Å—Ç–æ—Ä–∏–π –ø–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–π –∑–∞–≥—Ä—É–∑–∫–∏
      handleCancelPreview();
    } catch (error) {
      console.error('Error uploading story:', error);
      alert('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∏—Å—Ç–æ—Ä–∏–∏');
    }
  };

  const handleToggleSound = (e) => {
    e.stopPropagation();
    setIsMuted(!isMuted);
  };

  const previewMediaType = previewFile?.type.startsWith('video') ? 'video' : 'image';

  const areAllStoriesViewed = (storyGroup) => {
    return storyGroup.stories.every(story => viewedStories.has(story.id));
  };

  const updateViewedStories = async (storyId) => {
    // –°–æ–∑–¥–∞—ë–º –Ω–æ–≤—ã–π Set, —á—Ç–æ–±—ã React –≤—Å–µ–≥–¥–∞ —É–≤–∏–¥–µ–ª –∏–∑–º–µ–Ω–µ–Ω–∏–µ
    setViewedStories(prev => new Set([...prev, storyId]));
    try {
      await viewStory(storyId);
      setStories(prevStories => prevStories.map(storyGroup => ({
        ...storyGroup,
        stories: storyGroup.stories.map(story =>
          story.id === storyId
            ? { ...story, view_count: (story.view_count || 0) + 1 }
            : story
        )
      })));
    } catch (e) {
      // ignore
    }
  };

  // –ü–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ –Ω–∞ —Å–ª–µ–¥—É—é—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  const handleNextUser = () => {
    if (currentUserIndex < flattenedStories.length - 1) {
      setCurrentUserIndex(currentUserIndex + 1);
    } else {
      setCurrentUserIndex(null); // –ó–∞–∫—Ä—ã—Ç—å viewer
    }
  };

  // –£–¥–∞–ª–µ–Ω–∏–µ –∏—Å—Ç–æ—Ä–∏–∏ –∏–∑ –º–∞—Å—Å–∏–≤–∞ stories
  const handleStoryDeleted = (deletedId) => {
    setStories(prevStories => {
      const updatedStories = prevStories.map((sg, idx) => {
        if (idx !== currentUserIndex) return sg;
        return {
          ...sg,
          stories: sg.stories.filter(story => story.id !== deletedId)
        };
      }).filter(sg => sg.stories.length > 0); // –£–¥–∞–ª—è–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è, –µ—Å–ª–∏ –Ω–µ –æ—Å—Ç–∞–ª–æ—Å—å –∏—Å—Ç–æ—Ä–∏–π
      // –ï—Å–ª–∏ —É —Ç–µ–∫—É—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–µ –æ—Å—Ç–∞–ª–æ—Å—å –∏—Å—Ç–æ—Ä–∏–π ‚Äî –ø–µ—Ä–µ–π—Ç–∏ –∫ —Å–ª–µ–¥—É—é—â–µ–º—É
      if (
        updatedStories.length > 0 &&
        (!updatedStories[currentUserIndex] || updatedStories[currentUserIndex].stories.length === 0)
      ) {
        setCurrentUserIndex(idx => (idx < updatedStories.length ? idx : null));
      } else if (updatedStories.length === 0) {
        setCurrentUserIndex(null);
      }
      return updatedStories;
    });
  };

  if (loading) {
    return (
      <StoriesCard>
        <Box sx={{ p: 2, display: 'flex', alignItems: 'center' }}>
          <CircularProgress size={20} sx={{ mr: 2 }} />
          <Typography variant="h6" sx={{ fontSize: '1.1rem' }}>
            –ó–∞–≥—Ä—É–∑–∫–∞...
          </Typography>
        </Box>
      </StoriesCard>
    );
  }

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º –æ—Ç–≤–µ—Ç API –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏
  if (!loading && !isOwnProfile && (!stories || stories.length === 0)) {
    return null;
  }

  return (
    <>
      <StoriesCard>
        <StoriesContainer>
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleFileChange}
            accept="image/*,video/*"
            style={{ display: 'none' }}
          />
          {/* Add Story Button - –ø–æ–∫–∞–∑—ã–≤–∞–µ–º —Ç–æ–ª—å–∫–æ –Ω–∞ —Å–≤–æ–µ–º –ø—Ä–æ—Ñ–∏–ª–µ */}
          {isOwnProfile && (
            <StoryItem>
              <AddStoryButton onClick={handleAddStory}>
                <AddIcon color="primary" />
              </AddStoryButton>
            </StoryItem>
          )}
          {/* Stories List */}
          {flattenedStories.map((story) => (
            <StoryItem 
              key={story.id}
              onClick={() => handleStoryClick(story.id)}
            >
              <StoryPreview>
                {story.preview_url ? (
                  <Box
                    component="img"
                    src={story.preview_url}
                    alt="story preview"
                    sx={{
                      width: '100%',
                      height: '100%',
                      objectFit: 'cover'
                    }}
                  />
                ) : (
                  <Box
                    sx={{
                      width: '100%',
                      height: '100%',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      background: 'transparent'
                    }}
                  >
                    <VideocamIcon sx={{ color: 'primary.main', fontSize: 32 }} />
                  </Box>
                )}
              </StoryPreview>
            </StoryItem>
          ))}
        </StoriesContainer>

        {/* Preview Modal */}
        {previewFile && previewUrl && (
          <Box 
            sx={{
              position: 'fixed',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              zIndex: 2000,
              background: 'rgba(0,0,0,0.9)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }} 
            onClick={handleCancelPreview}
          >
            <Box 
              onClick={(e) => e.stopPropagation()}
              sx={{
                width: '100%',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                position: 'relative',
                borderRadius: 2,
                overflow: 'hidden',
                maxWidth: 400,
                maxHeight: 600,
              }}
            >
              {/* Blurred Background */}
              <Box
                sx={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  backgroundImage: `url(${previewUrl})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                  filter: 'blur(20px)',
                  transform: 'scale(1.05)',
                  zIndex: -1,
                  opacity: 1,
                }}
              />
              {/* Header */}
              <Box
                sx={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  right: 0,
                  padding: 2,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  zIndex: 10,
                }}
              >
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1,
                    zIndex: 1,
                    maxWidth: 'calc(100% - 80px)',
                  }}
                >
                  <Avatar
                    src={user?.photo ? `/static/uploads/avatar/${user.id}/${user.photo}` : undefined}
                    alt={user?.name || 'You'}
                    sx={{
                      width: 32,
                      height: 32,
                      border: theme => `2px solid ${theme.palette.primary.main}`,
                      flexShrink: 0,
                    }}
                  />
                  <Box sx={{ overflow: 'hidden' }}>
                    <Typography 
                      variant="subtitle1" 
                      color="white" 
                      sx={{ 
                        fontWeight: 600, 
                        lineHeight: 1.2, 
                        whiteSpace: 'nowrap', 
                        textOverflow: 'ellipsis', 
                        overflow: 'hidden' 
                      }}
                    >
                      {user?.name || '–í–∞—à–∞ –∏—Å—Ç–æ—Ä–∏—è'}
                    </Typography>
                  </Box>
                </Box>
                <IconButton 
                  onClick={(e) => { 
                    e.stopPropagation(); 
                    handleCancelPreview(); 
                  }} 
                  sx={{ color: 'white', zIndex: 1 }}
                >
                  <CloseIcon />
                </IconButton>
              </Box>
              {/* Media Container */}
              <Box
                sx={{
                  flex: 1,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  position: 'relative',
                }}
              >
                {previewMediaType === 'image' ? (
                  <Box
                    component="img"
                    src={previewUrl}
                    alt="preview"
                    sx={{
                      maxWidth: '100%',
                      maxHeight: '100%',
                      objectFit: 'contain',
                      position: 'relative',
                      zIndex: 1,
                    }}
                  />
                ) : (
                  <>
                    <Box
                      component="video"
                      ref={videoRef}
                      src={previewUrl}
                      loop
                      autoPlay
                      playsInline
                      muted={isMuted}
                      sx={{
                        maxWidth: '100%',
                        maxHeight: '100%',
                        objectFit: 'contain',
                        position: 'relative',
                        zIndex: 1,
                      }}
                    />
                    <Box
                      sx={{
                        position: 'absolute',
                        bottom: 16,
                        right: 16,
                        display: 'flex',
                        alignItems: 'center',
                        gap: 1,
                        zIndex: 1000,
                      }}
                    >
                      <IconButton
                        onClick={handleToggleSound}
                        sx={{
                          color: 'white',
                          backgroundColor: 'rgba(0, 0, 0, 0.5)',
                          '&:hover': {
                            backgroundColor: 'rgba(0, 0, 0, 0.7)',
                          },
                          width: 32,
                          height: 32,
                          padding: 0,
                        }}
                      >
                        {isMuted ? <VolumeOffIcon fontSize="small" /> : <VolumeUpIcon fontSize="small" />}
                      </IconButton>
                    </Box>
                  </>
                )}
              </Box>
              {/* Action Buttons */}
              <Box
                sx={{
                  position: 'absolute',
                  bottom: 16,
                  left: 0,
                  right: 0,
                  display: 'flex',
                  justifyContent: 'center',
                  gap: 2,
                  zIndex: 100,
                }}
              >
                <Button 
                  variant="contained" 
                  color="primary" 
                  onClick={(e) => { 
                    e.stopPropagation(); 
                    handlePublishStory(); 
                  }}
                >
                  –û–ø—É–±–ª–∏–∫–æ–≤–∞—Ç—å
                </Button>
                <Button 
                  variant="outlined" 
                  color="secondary" 
                  onClick={(e) => { 
                    e.stopPropagation(); 
                    handleCancelPreview(); 
                  }}
                >
                  –û—Ç–º–µ–Ω–∞
                </Button>
              </Box>
            </Box>
          </Box>
        )}
      </StoriesCard>

      {/* Story Viewer */}
      {currentUserIndex !== null && flattenedStories[currentUserIndex] && (
        <StoryViewer
          userStories={{
            user: flattenedStories[currentUserIndex].user,
            stories: [flattenedStories[currentUserIndex]]
          }}
          currentUserIndex={0}
          setCurrentUserIndex={setCurrentUserIndex}
          onClose={handleCloseStory}
          currentUserId={flattenedStories[currentUserIndex]?.user?.id}
          onStoryDeleted={handleStoryDeleted}
          isMuted={isMuted}
          onToggleSound={handleToggleSound}
          isMobile={isMobile}
          onStoryViewed={updateViewedStories}
          onNextUser={handleNextUser}
        />
      )}
    </>
  );
};

export default Stories; 